import React, { Fragment } from "react";

// component
import { Container, Row, Col, Image } from "react-bootstrap";
import { useLocation } from "react-router-dom";
import { isMobile } from "react-device-detect";
import dateFormat from "dateformat";

export default function PageEventDetails() {
  const location = useLocation();
  const data = location.state;

  return (
    <Fragment>
      <Container
        fluid
        className={`${isMobile ? "" : "py-5"} m-0 p-0 w-100 h-75`}
      >
        <Row className="h-100 m-0 p-0">
          <Col lg={12} className="d-flex justify-content-center p-0 m-0">
            <iframe
              title="wtbs google map"
              className="w-100 event-google-map"
              src={data.content_url}
              style={{ border: 0 }}
              allowFullScreen={true}
              loading="lazy"
              referrerPolicy="no-referrer-when-downgrade"
            ></iframe>
          </Col>
        </Row>
      </Container>

      <Container fluid className="m-0 p-0 w-100 h-auto">
        <Row className="m-0 p-0 w-100 h-auto justify-content-center">
          <Col
            lg={12}
            className={`${
              isMobile ? "w-100" : "w-75"
            } m-0 p-0 justify-content-center`}
          >
            <Row className="m-0 p-0 w-100 h-100">
              <Col lg={8} className="m-0 p-0">
                <Row className="m-0 p-0 w-100 h-75">
                  <Col lg={12} className="m-0 p-0">
                    <Image
                      className="event-details-image"
                      src={
                        process.env.REACT_APP_API_URL +
                        "/images/contents/" +
                        data.content_thumbnail
                      }
                    />
                  </Col>
                </Row>
              </Col>
              <Col lg={4} className="pt-3 pt-lg-0">
                <Container className="m-0 p-0 event-details-shadow">
                  <Row className="m-0 p-0 ps-4 pt-5 w-100">
                    <Col
                      lg={12}
                      className="event-details-border d-flex align-items-center"
                    >
                      <h1 className="event-details-header-txt">
                        Event Details
                      </h1>
                    </Col>
                  </Row>
                  <Row className="m-0 p-0 ps-4 pt-3 w-100">
                    <Col lg={12} className="event-details-text-border">
                      <ul className="m-0 p-0">
                        <li className="d-flex justify-content-between">
                          <p className="event-details-text">Date: </p>
                          <span className="">
                            {dateFormat(data.content_schedule, "mmmm dd, yyyy")}
                          </span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="event-details-text">Time:</p>
                          <span className="">
                            {data.content_start_time} - {data.content_end_time}
                          </span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="event-details-text">Phone:</p>
                          <span className="">9089089890</span>
                        </li>
                        <li className="d-flex justify-content-between">
                          <p className="event-details-text">Email:</p>
                          <span className="">test@gmail.com</span>
                        </li>
                      </ul>
                    </Col>
                  </Row>
                </Container>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row className="m-0 p-0 w-100 h-auto justify-content-center">
          <Col lg={12} className="m-0 p-0 w-75 justify-content-center">
            <Row className="m-0 p-0 w-100 h-100">
              <Col lg={8} className="m-0 p-0">
                <Row className="m-0 p-0 w-100 h-75">
                  <Col
                    lg={12}
                    className={`${isMobile ? "text-center" : ""} m-0 p-0`}
                  >
                    <h1 className="event-details-body-header pt-5">
                      {data.content_title}
                    </h1>
                  </Col>
                  <Col
                    lg={12}
                    className="m-0 p-0"
                    dangerouslySetInnerHTML={{
                      __html: data.content_description.content_description,
                    }}
                  ></Col>
                </Row>
              </Col>
              <Col lg={4}></Col>
            </Row>
          </Col>
        </Row>
      </Container>
      <Container fluid className="m-0 p-0 h-15"></Container>
    </Fragment>
  );
}
